*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  position: relative;
  background-color: rgba(151, 151, 151, 0.779);
  background-image: url(/public/img/beach_bg.webp);
  background-size: cover;
  background-attachment: fixed;
  box-shadow: inset 0 0 0 2000px rgba(0, 0, 0, 0.584);
  color: white;
  font-family: 'Bangers', cursive;
  overflow-x: hidden;
}

.header-txt,
h1 {
  font-size: 30px;
  font-weight: bold;
  padding: 1em 0;
  letter-spacing: 3px;
}

p {
  font-size: 25px;
  letter-spacing: 1px;
}

ul {
  list-style: none;
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.center-between {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.trans {
  transition: all ease-in-out .5s;
}

hr {
  background-image: linear-gradient(90deg, transparent 0%, white 50% , transparent 100%);
  border: 0;
  height: .3em;
}

button {
  border: none;
  padding: 1em 2em;
  border-radius: 10px;
  background: linear-gradient(60deg, rgb(255, 255, 255) 20%, rgba(0,0,0,0) 100%);
  cursor: pointer;
  transition: .2s ease-in-out all;
  font-size: 16px;
  font-weight: bold;
  color: black;
}

button:active {
  background: white;
}

nav {
  position: fixed;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: space-around;
  background: rgb(0,0,0);
  background: linear-gradient(180deg, rgba(0,0,0,1) 53%, rgba(0,0,0,0) 100%);
  color: white;
  padding: 1em;
  z-index: 999;
}

nav .logo img {
  width: 3em;
  padding: 0 .5em;

}

nav ul {
  font-size: 20px;
  font-weight: bold;
}

nav ul li {
  padding: 0 .5em;
  letter-spacing: 2px;
  cursor: pointer;
}

nav ul li:hover {
  color: rgb(0, 0, 193);
}

section {
  padding: 2em;
  justify-content: space-between;
}

.hero-section {
  background: linear-gradient(180deg, rgba(0,0,0,1) 74%, rgba(0,0,0,0) 100%);
  height: 100vh;
  justify-content: space-around;
}

.hero-section img {
  width: 25em;
}

.hero-section .content {
  width: 50%;
  padding: 0 0 0 5em;
}

.hero-section .content h1 {
  font-size: 50px;
}



.hero-section .content p,
.hero-section .content ul li{
  font-size: 50px;
  list-style: initial;
  text-align: start;
  justify-content: flex-start !important; 
}

.hero-section .content ul {
  margin-bottom: 1em;
}

.hero-section .content ul li img {
  width: 1em;
}

.head-txt {
  margin-bottom: 1em;
}


.head-txt h1 {
  font-size: 40px;
  /* color: blue; */
  background: -webkit-linear-gradient(160deg, red 30%, blue 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  user-select: none;
}


.about-sec {
  background: linear-gradient(180deg, rgba(0,0,0,0) 0%, rgb(236 236 236) 40%);
}

.about-sec .head-txt h1 {
  color: white;
}

.high-word {
  font-size: 50px;
  /* color: blue; */
  background: -webkit-linear-gradient(#1900ff, #0e0b3a);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.about-sec .content {
  width: 45%;
  align-items: flex-start;
  color: black;
}

.about-sec img {
  width: 45%;
  border-radius: 2em;
}

.about-sec .content p {
  margin-bottom: 1em;
}

.skills-sec {
  letter-spacing: 2px;
  /* background-image:  linear-gradient(180deg, rgb(0, 0, 0) 80%, rgba(255, 255, 255, 0) 100%); */
  background-color: rgba(0, 0, 0, 0.681);
  padding: 0 0 5em 0;

}

.skills-sec .skill-card_container {
  margin-top: 3em;
  height: 40vh;
}

.about-sec .content .skill-card {
  text-align: center;
  margin: 0 2em;
  transition: all ease-in-out .5s;
  width: 15em;
  height: 10em;
}

.hover {
  border: 7px solid;
  border-image: linear-gradient(135deg,#000988 0%,#ffffff00 40%) 8;
}

.hover:hover {
  animation: card-border-animaiton 1s infinite ease-in;
}

@keyframes card-border-animaiton {
  0% {border-image: linear-gradient(135deg,#000988 0%,#ffffff00 40%) 8;}
  25% {border-image: linear-gradient(228deg,#000988 0%,#ffffff00 40%) 8;}
  50% {border-image: linear-gradient(315deg,#000988 0%,#ffffff00 40%) 8;}
  75% {border-image: linear-gradient(41deg,#000988 0%,#ffffff00 40%) 8;}
  100% {border-image: linear-gradient(135deg,#000988 0%,#ffffff00 40%) 8;}
  /* 0% {border-image: linear-gradient(135deg,#000988 0%,#ffffff00 50%) 8;}
  50% {border-image: linear-gradient(135deg,#ffffff00 50%,#000988 100%) 8;}
  100% {border-image: linear-gradient(135deg,#000988 0%,#ffffff00 50%) 8;} */
}

.about-sec .content .skill-card:hover {
  transform: scale(1.2);
}

.about-sec .content .skill-card img {
  transition: all ease-in-out 1s;
  width: 9em;
  height: 9em;
  padding: 1em;
  margin-bottom: 1em;
}

.skills-sec .skill-card_container .skill-card {
  background-color: rgb(235, 235, 235);
  background-image: linear-gradient(160deg, white 50%, gray 100%);
  padding: .5em;
  width: 12em;
  height: 10em;
  margin: 0 1em;
  border-radius: 20%;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  user-select: none;
  
}

.skills-sec .skill-card_container .skill-card:hover {
  transform: scale(.9);
}

.skills-sec .skill-card_container .skill-card img {
  width: 5em;

}

.skills-sec .skill-card_container .skill-card h2 {
  color: black;
  margin-top: .4em;
}



.pic-sec {
  background-image:  linear-gradient(0deg, rgba(0,0,0,0) 0%, rgba(0, 0, 0, 0.692) 40%);
}

.pic-sec .pic_container {
  margin-top: 2em;
}

.pic-sec .head-txt h1 {
  font-size: 40px;
  /* color: blue; */
  background: -webkit-linear-gradient(135deg, red, blue);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.pic-sec .pic_container li {
  margin: 2em;
  width: 15em !important;
}

.pic-sec .pic_container .pic-card {
  width: 15em;
  margin: 0 .5em;
  border-radius: 10%;
  user-select: none;
  background-image: linear-gradient(135deg, purple 30%, blue 110% );
  transition: all .1s ease-in-out ;
}

.pic-sec .pic_container .pic-card:hover {
  animation: card-shadow .5s infinite ease-in-out;
}

@keyframes card-shadow {
  0% {
    box-shadow: 5px 4px 17px 0 #000988c4;}
  25% 
  {box-shadow: -5px 4px 17px 0 #000988c4;
  }
  50% 
  {box-shadow: -5px -4px 17px 0 #000988c4;}
  75% 
  {box-shadow: 5px -4px 17px 0 #000988c4;
  }
  100%
   {box-shadow: 5px 4px 17px 0 #000988c4;}
}

.pic-sec .pic_container .pic-card img {
  width: 100%;
  height: 15em;
  border-radius: 10% 10% 0 0;
  pointer-events: none;
}

.pic-sec .pic_container .pic-card h1 {
  color: rgb(255, 255, 255);
  border-radius: 20px;
  padding: .5em 0;
}

.pic-sec .pic_container .pic-card p {
  transform: translateY(20%);
  opacity: 0;
  transition: all ease-in-out .5s;
  padding: .2em 0;
}

.pic-sec .pic_container .pic-card:hover p {
  transform: translateY(0%);
  opacity: 1;
}


footer {
  padding: 2em 5em;
  background: rgb(0,0,0);
  background: linear-gradient(180deg, rgba(0,0,0,0) 0%, rgba(0,0,0,1) 40%);
}

footer .logo img {
  width: 5em;
  padding: 0 1em;
}

footer ul li {
  text-align: start;
  justify-content: flex-start !important; 
  transition: .5s all ease-in-out;
  cursor: default;
}

footer ul li:hover {
  transform: scale(1.2);
}

footer ul li span img {
  width: 3em;
  padding: 0 .5em;
}
